<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process279"></a>

<p><br></p>
<p><span class="rvts17">终结时刻（End Times）[主线任务]：</span></p>
<p><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1527.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">进入神圣殿堂后，马拉迪，塔昆，阿玛蒂亚，加雷斯都会来为我们祝福，之后在与各路大神的战斗中，源力消耗会降至很低，因此使用最厉害的源力技能将会使战斗达到最好的效果。但是让这些人来捧场，需要完成他们的对应任务：</span>
<br><span class="rvts20">1. </span><span class="rvts21">马拉迪</span><span class="rvts20"> -- </span><span class="rvts21">她总会到场。</span>
<br><span class="rvts20">2. </span><span class="rvts21">塔昆</span><span class="rvts20"> -- </span><span class="rvts21">完成</span><span class="rvts20"></span><span class="rvts21">家族宝藏（</span><span class="rvts20">All in Family</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线和</span><span class="rvts20"></span><span class="rvts21">重生的国王（</span><span class="rvts20">A King Reborn</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线。</span>
<br><span class="rvts20">3. </span><span class="rvts21">加雷斯</span><span class="rvts20"> -- </span><span class="rvts21">完成</span><span class="rvts20"></span><span class="rvts21">转角遇到危险（</span><span class="rvts20">Most Dangerous when Cornered</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线，</span><span class="rvts20"></span><span class="rvts21">埋葬过去（</span><span class="rvts20">Burying the Past</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线，</span><span class="rvts20"></span><span class="rvts21">寻机报仇（</span><span class="rvts20">Seeking Revenge</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线。注意，</span><span class="rvts20"></span><span class="rvts21">寻机报仇</span><span class="rvts20"></span><span class="rvts21">支线需要成功把加雷斯劝到复仇女神号才算以好的结局关闭。</span>
<br><span class="rvts20">4. </span><span class="rvts21">阿玛蒂亚</span><span class="rvts20"> -- </span><span class="rvts21">完成</span><span class="rvts20"></span><span class="rvts21">阿玛蒂亚的请求（</span><span class="rvts20">Almadia's Request</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线，和</span><span class="rvts20"></span><span class="rvts21">自由之钥（</span><span class="rvts20">A Key to Freedom</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1528.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">接下来就是遇到卢西安神明，达利斯大锤，和一个</span><span class="rvts20"></span><span class="rvts21">蒙面护卫</span><span class="rvts20"></span><span class="rvts21">。这个蒙面护卫其实就是布拉克斯</span><span class="rvts20"></span><span class="rvts21">雷克斯（</span><span class="rvts20">Braccus Rex</span><span class="rvts21">），他是被达利斯复活的。此时一系列较长的对话把剧情最终引向两个可能的路线：</span>
<br><span class="rvts20">1. </span><span class="rvts21">接受牺牲源力：</span>
<br><span class="rvts21">接受牺牲源力会使得布拉克斯瞬间觉醒，他会坦白他偷走了达利斯的束缚魔杖，此时所有在场的人都会与布拉克斯抗衡。布拉克斯会召唤出虚空兽王。虚空兽王又会召唤出很多我们之前遇到的那些投靠了黑环的坏人，像是伊泽贝尔，瘦黄男人，林德</span><span class="rvts20"></span><span class="rvts21">凯姆等。。如果之前你杀掉这些人的时候，没有吸走他们鬼魂的源力，那么他们都会被召唤出来。随着战斗时间不断推进，虚空兽王会召唤出更多的黑环将士。但是在这场终极</span><span class="rvts20">BOSS</span><span class="rvts21">战中，布拉克斯才是应该第一个被杀掉的。只要集火杀掉布拉克斯，游戏到此终结，进入结尾剧情。</span>
<br><span class="rvts20">2. </span><span class="rvts21">拒绝牺牲源力：</span>
<br><span class="rvts21">拒绝牺牲源力将会引起所有在场人的反对，一场难打的战斗摆在眼前。我们不仅需要对抗神明卢西安，更要对抗大锤。然而只要击杀他们中的一个便可以使剧情继续往前推进。之后，布拉克斯反了，他会攻击在场的所有人，就像上面的</span><span class="rvts20"></span><span class="rvts21">接受源力牺牲</span><span class="rvts20"></span><span class="rvts21">的剧情一样，更艰难的战斗等待着我们。该结局会以杀掉大锤达利斯或卢西安关闭。注意，在这场战斗中，如果队伍中有角色死亡，那么尽可能的在杀掉布拉克斯之前复活队友，因为如果队友未在战斗结束前被复活，那么在之后</span><span class="rvts20"></span><span class="rvts21">修复的复仇女神号</span><span class="rvts20"></span><span class="rvts21">上将不会找到队友，也不会在最后看到队友的结局动画。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1529.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">如果选择牺牲源力，那么在杀掉布拉克斯之后，大锤会在结尾告诉你她精心谋划的一切都失败了。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1530.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">在所有战斗都结束后，就要进入故事的结尾剧情选择。这个选择很重要，因为会选出</span><span class="rvts20">5</span><span class="rvts21">种不同的结局（详见结局篇）。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1531.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">无论选择怎样的结局，都将引起不同的后果。但是在笔者看来，算得上是好结局的内容需要包含</span><span class="rvts20"></span><span class="rvts21">消除黑环，对抗神王</span><span class="rvts20"></span><span class="rvts21">以及</span><span class="rvts20"></span><span class="rvts21">天下大稳</span><span class="rvts20"></span><span class="rvts21">。因此理论上来说，选择</span><span class="rvts20"></span><span class="rvts21">禁用源力</span><span class="rvts20"></span><span class="rvts21">的结局算是最好的。最差的结局内容就是</span><span class="rvts20"></span><span class="rvts21">神王一统天下</span><span class="rvts20"></span><span class="rvts21">，这样的结局有</span><span class="rvts20"></span><span class="rvts21">择爱弃神</span><span class="rvts20"></span><span class="rvts21">以及作为主角费恩的</span><span class="rvts20"></span><span class="rvts21">延续永恒</span><span class="rvts20"></span><span class="rvts21">独立结局。</span>
<br><span class="rvts21">当一切结束后，游戏进入结尾动画，结尾动画将描述你选择的不同结局内容，以及之后回到</span><span class="rvts20"></span><span class="rvts21">被修复的复仇女神号</span><span class="rvts20"></span><span class="rvts21">上时，</span><span class="rvts20">NPC</span><span class="rvts21">和队友对你的评价。之后告诉复仇女神号船头的龙头，你要开始新的旅途，那么游戏进入最终的感谢画面。感谢画面中包括你在游戏中救过或放过的一些重要人物的结局，以及世界地图上的一些发展结局。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



